<template>
  <BaseLayout>
    <Breadcrumbs />
    <section
      v-if="currentstore != null"
      class="our-shop--banner pb-25 pb-lg-35"
    >
      <BContainer>
        <h1 class="font-weight-normal mb-30 mb-lg-35">
          {{ currentstore?.name }}
        </h1>
        <div v-if="currentstore.banner_image != null" class="banner-img">
          <img :src="currentstore?.banner_image?.url" alt="Our Shop" />
        </div>
      </BContainer>
    </section>
    <section v-if="currentstore != null" class="content pb-7">
      <BContainer>
        <div v-html="currentstore?.description"></div>
      </BContainer>
    </section>
    <SubscribeSeparator />
    <section
      v-if="currentstore != null"
      class="address-and-opening-time pb-35 py-lg-10"
    >
      <BContainer>
        <BRow>
          <BCol lg="4">
            <span class="h4 d-block">Openingstijden winkel</span>
            <ul class="opening-time list-unstyled pl-0 m-0 pb-30">
              <li
                v-for="openingTimeDatum of openingTimes"
                :key="openingTimeDatum.label"
              >
                <div
                  class="d-flex"
                  style="width: 100%"
                  v-for="(item, index) in openingTimeDatum.hours"
                  :key="`${openingTimeDatum.label}-${index}`"
                >
                  <span class="day d-block mr-25">
                    <span v-if="index == 0">{{
                      $t(openingTimeDatum.label)
                    }}</span>
                  </span>
                  <span v-if="item.from != null" class="time"
                    >{{ item.from }} - {{ item.to }}</span
                  >
                  <span v-if="item.from == null" class="time d-block">{{
                    $t("closed")
                  }}</span>
                </div>
              </li>
            </ul>
            <span class="h4 d-block">Adres:</span>
            <address v-if="currentstore != null">
              {{ currentstore?.name }} <br />
              {{ currentstore?.street }}, <br />
              {{ currentstore?.postcode }} {{ currentstore?.city }} <br />
              Telefoon {{ currentstore.phone }}
            </address>
          </BCol>
          <BCol lg="8" class="pt-5">
            <div
              v-if="currentstore.map_image != null"
              class="map-holder"
              :style="{
                backgroundImage: `url(${currentstore?.map_image.url})`,
              }"
            ></div>
          </BCol>
        </BRow>
      </BContainer>
    </section>
  </BaseLayout>
</template>

<script setup>
import { onMounted, onBeforeUnmount, ref, computed, watch } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import { useHead } from "@unhead/vue";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import Breadcrumbs from "@/base/core/components/Breadcrumbs";
import SubscribeSeparator from "@/base/core/components/SubscribeSeparator.vue";

const store = useStore();
const route = useRoute();

const largeScreen = ref(false);
const currentstore = computed(() =>
  store.getters["stores/getStoreByCode"](route.params.code)
);
const openingTimes = computed(() =>
  currentstore.value ? currentstore.value.openingHours : null
);

const setLargeScreen = () => {
  largeScreen.value = window.innerWidth > 991;
};

const updateBreadcrumbs = () => {
  if (currentstore.value) {
    const bcrumb = {
      current: currentstore.value?.name,
      routes: [{ name: "Winkels", route_link: "/winkels" }],
    };
    store.commit("breadcrumbs/set", bcrumb);
  }
};

// Watch for changes in the current store
watch(currentstore, (newVal) => {
  if (newVal) {
    updateBreadcrumbs();
  }
});

// Set up meta information using useHead
useHead({
  title: () => currentstore.value?.name,
  meta: [
    { name: "title", content: () => currentstore.value?.name },
    { name: "keywords", content: () => currentstore.value?.name },
    { name: "description", content: () => currentstore.value?.name },
  ],
});

onMounted(() => {
  updateBreadcrumbs();
  window.addEventListener("resize", setLargeScreen);
  window.addEventListener("load", setLargeScreen);
  setLargeScreen();
});

onBeforeUnmount(() => {
  window.removeEventListener("resize", setLargeScreen);
  window.removeEventListener("load", setLargeScreen);
});
</script>
